import moment from 'moment'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
export default{
	mixins: [ common,globals ],
	data () {
		return {
			data_uri: 'review_students',
			data: [],				
		}
	},
	mounted () {
		
	},
	methods: {
		formatDate (time) {
			moment.locale('zh-cn')
			return moment(time).fromNow()
		},
		redirect (row) {
			this.$router.push({path: '/reviews/'+row.rs_id})
		}
	}
}



